
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.pick-content{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 80%;
  height: 360px;
  background-color: #ffffff;
}
.content {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.normal{
  font-weight: 100;
}
.active{
  font-weight: 900;
  background-color: #dbdbdb;
  border-radius: 30px;
}
.none{
  display: none;
}
.block{
  display: block;
}
.scroll-content {
  width: 100%;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  overflow: scroll;
  z-index: 2;
  height: 300px;
}
.scroll-ul {
  padding: 0;
  width: 100%;
  z-index: 1;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  margin-top: 120px;
  margin-bottom: 120px;
}
.scroll-li {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 25px;
  line-height: 60px;
  height: 60px;
}
.cover_top {
  background-color: #fff;
  width: 100%;

  z-index: 3;
  opacity: 0.6;
  border-bottom: 2px solid grey;
  pointer-events: none;
  -ms-touch-action: none;
}
.cover_bottom {
  background-color: #fff;
  width: 100%;
  z-index: 3;
  opacity: 0.6;
  border-top: 2px solid grey;
  pointer-events: none;
  -ms-touch-action: none;
}
.button-group{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.button-group button{
  margin: 20px;
  background-color: white;
  border: 1px grey solid;
  border-radius: 5px;
  padding: 5px 20px;
  outline: none;
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
}

