  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
